/* 기본 스타일 */
.sidebar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 40px;
  background-color: #6a7ba2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px;
  z-index: 10;
}

.sidebar nav {
  flex-grow: 1;
}

.sidebar nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 50px;
  margin-left: 60px;
  padding: 0;
}

.sidebar nav ul li {
  margin-left: 0;
}

.sidebar-link {
  font-size: 15px;
  font-family: 'chab';
  background-color: rgba(0, 0, 0, 0);
  border: none;
  color: #ffc8f4;
}

.sidebar-link:hover {
  color: hotpink;
  cursor: pointer;
}

.portfolio-icon {
  font-family: 'PyeongChangPeace-Bold';
  font-size: 17px;
  text-decoration: none;
  color: orange;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.portfolio-icon:hover {
  color: hotpink;
}

.menu-container {
  display: none; /* 기본적으로 숨김 */
}

.menu-icon {
  margin-right:60px;
  background: none;
  border: none;
  font-size: 30px;
  color: #ffc8f4;
  cursor: pointer;
}

.menu-icon:hover {
  color: hotpink;
}

.dropdown-menu {
  position: absolute;
  top: 40px;
  right: 20px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  padding: 10px;
}

.dropdown-item {
  background: none;
  border: none;
  padding: 8px 12px;
  text-align: left;
  font-size: 14px;
  color: #6a7ba2;
  cursor: pointer;
  width: 100%;
}

.dropdown-item:hover {
  color: hotpink;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .sidebar nav {
    display: none;
  }

  .menu-container {
    display: flex;
    align-items: center;
  }
}
