.contact-section {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #ffdcf639;
    border-radius: 8px;

    width: 90%; /* 기본 너비 (화면 크기에 따라 줄어듦) */
    max-width: 500px; /* 최대 너비 */
    padding-bottom: 100px;
    margin: 50px auto; /* 위아래 중앙 정렬 */
    margin-bottom: 100px; /* 하단 여백 */
  }
  
  .contact-section h1 {
    font-size: 1.8rem;
    margin-bottom: 15px;
    color: #333;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 15px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
  }
  
  .form-group label {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    align-items: center;
    align-self: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
  }
  
  .form-input:focus,
  .form-textarea:focus {
    border-color: #4a90e2;
    box-shadow: 0 0 4px rgba(255, 0, 157, 0.4);
  }
  
  

  .submit-button {
    background-color: #ff7961;
    color: white;
    font-size: 14px;
    padding: 8px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    text-transform: uppercase;
    align-self: center; /* 버튼을 중앙으로 */
    width: 100%; /* 기본 너비 */
    max-width: 200px; /* 최대 너비 */
  }
  
  .submit-button:hover {
    background-color: hotpink;
  }
  
  /* 반응형 미디어 쿼리 */
  @media (max-width: 768px) {
    .contact-section {
      padding: 15px;
      margin: 30px auto;
    }
  
    .contact-section h1 {
      font-size: 1.5rem;
    }
  
    .submit-button {
      font-size: 12px;
      padding: 10px;
      max-width: 150px;
    }
  }
  
  @media (max-width: 480px) {
    .contact-section {
      padding: 10px;
      margin: 20px auto;
    }
  
    .contact-section h1 {
      font-size: 1.2rem;
    }
  
    .form-group input,
    .form-group textarea {
      font-size: 12px;
      padding: 8px;
    }
  
    .submit-button {
      font-size: 10px;
      padding: 8px;
      max-width: 120px;
    }
  }
  