.activities-section {
  margin:80px auto;
  position: relative;
  z-index: 1;
  border: 5px dotted pink;
  border-radius: 100px;
  margin: 200px auto;
  padding: 40px 20px;
  text-align: center;
  max-width: 1200px;
  box-sizing: border-box;
  }
  
  .activities-grid {
    display: grid;
    grid-template-columns: repeat(2,1fr); /* 2열 레이아웃 */
    gap: 20px;
    justify-content: center;
  }
  
  .activity-card {
    font-family: 'Moneygraphy-Rounded';
    font-weight: bold;
    width:400px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 255, 204, 0.499);
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 250px;
    z-index: 1;
    text-align: left;
  }
  
  .activity-header {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .activity-badge {
    
    background-color: #ff7961;
    color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: bold;
  }
  
  .activity-date {
    font-size: 14px;
    color: #666;
  }
  
  .activity-body {
    flex: 1;
    margin-bottom: 10px;
  }
  
  .activity-body p {
    font-size: clamp(11px, 1.5vw, 14px);
    line-height: 1.5;
    color: #333;
    word-wrap: break-word; /* 긴 단어 줄바꿈 */
    white-space: normal; /* 줄바꿈 허용 */
  }
  
  .activity-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
  }
  
  .activity-tag {
    background-color: #f0f0f0;
    color: #333;
    padding: 5px 8px;
    border-radius: 5px;
    font-size: 12px;
  }
  
  .activity-footer {
    display: flex;
    gap: 10px;
    justify-content: flex-start;
  }
  
  .activity-link {
    font-size: 14px;
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
  }
  
  .activity-link:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 1000px) {
    .activities-grid {
      grid-template-columns: repeat(1, 1fr); /* 한 줄에 한 개씩 */
    }
  
    .activity-card {
      width: 300px !important; /* 더 작은 크기 */
      height: 200px !important;
      margin: 0 auto; /* 카드 중앙 정렬 */
    }
  }