/* 기본 스타일 */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  overflow-x: hidden;
  height: 100%; 
  background: #6A7BA2 ;
  cursor: none; 
}




.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  cursor: none; 
  
}

.cursor {
  filter: drop-shadow( 0px 0px 5px yellow);
  position: absolute;
  width: 50px; /* star.png 크기 */
  height: 50px;
  pointer-events: none; /* 이미지가 마우스 이벤트에 영향을 주지 않도록 설정 */
  transform: translate(-50%, -50%); /* 마우스 중앙에 이미지 위치 */
  z-index: 9999; /* 가장 위에 위치 */
}

.background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  overflow: hidden;
 /* 콘텐츠 위로 올라오지 않도록 설정 */
  pointer-events: none;
  
}

.background span {
  width: 20vmin;
  height: 20vmin;
  border-radius: 20vmin;
  backface-visibility: hidden;
  position: absolute;
  animation: move infinite linear;
}

@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}

/* 각 span의 개별 스타일은 이미 제공된 nth-child를 그대로 사용 */
