.section-divider {
  border: none;
  border-top: 2px dashed orange; /* 점선 스타일 */
  margin: 150px 0;
  width: 80%; /* 원하는 너비 설정 */
  margin-left: auto; /* 가운데 정렬 */
  margin-right: auto;
  opacity: 0.8; /* 투명도 설정 */
}


