.skills-section {
  position: relative;
  z-index: 1;
  border: 5px dotted pink;
  border-radius: 100px;
  margin: 160px auto;
  padding: 20px;
  text-align: center;
  max-width: 1200px;
  box-sizing: border-box;
}

/* 🔥 핵심: 아이템 개수 상관없이 중앙 정렬 + 줄바꿈 */
.skills-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* 항상 가운데 정렬 */
  gap: 20px;
  margin-bottom: 40px;
}

.skill-item {
  width: 120px;
  aspect-ratio: 1 / 1; /* 정사각형 */
  box-sizing: border-box;
  position: relative;
}

/* 카드 스타일 */
.skill-icon-wrapper {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 3px 10px rgba(0, 76, 255, 0.66);
  transition: transform 0.3s ease;
}

.skill-icon-wrapper:hover {
  transform: scale(1.1);
}

.skill-icon {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hover-overlay {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.skill-icon-wrapper:hover .hover-overlay {
  opacity: 1;
}

.hover-text {
  font-size: 14px;
  text-align: center;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

/* ✅ 반응형 대응: 아이템 작게 */
@media screen and (max-width: 768px) {
  .skill-item {
    width: 100px;
  }

  .skills-grid {
    gap: 12px;
  }
}

@media screen and (max-width: 480px) {
  .skill-item {
    width: 90px;
  }

  .skills-grid {
    gap: 10px;
  }
}
