.about-wrapper {
  font-size: clamp(0.6rem, 2vw, 1rem);
    position: relative;
    width: 100%;
    max-width: 900px; /* 이미지 크기에 맞게 조정 */
    margin: 0 auto;
    overflow-x: hidden;
    transform-origin: center; /* 회전 중심 설정 */
  transition: transform 0.5s ease-in-out; /* 부드러운 회전 효과 */
  }
  

  .about-section {
    padding: 40px;
    position: relative;
    z-index: 1;
  }
  
  .background-image {
    width: 100%;
    height: auto;
    display: block;
    position: relative;
    pointer-events: none;
  }
  
  .about-text-wrapper {
    font-family: 'Moneygraphy-Rounded';
    position: absolute;
    top: 10%; /* 텍스트 위치 조정 */
    left: 65%;
    width: 80%; /* 이미지 안에서의 텍스트 영역 */
    max-width: 290px; /* 텍스트 최대 너비 */
    text-align: left;
    padding: 10px;
    border-radius: 5px;
    word-wrap: break-word; /* 긴 단어를 자동 줄바꿈 */
    box-sizing: border-box; /* 패딩을 포함한 영역 계산 */
    border-radius: 10px;
  }
  
  .about-text-wrapper ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .about-text-wrapper ul ul {
    margin-left: 20px;
  }
  
  .about-text-wrapper p {
    line-height: 1.5; /* 줄 간격 */
  word-break: break-word; /* 텍스트 잘림 방지 */
    font-size: clamp(0.1rem, 1.5vw, 0.8rem);
    margin-top: 10px;
    line-height: 1.6;
    font-size: 1rem; /* 글씨 크기 조정 */
  }
  
  @media (max-width: 800px) {
    .about-text-wrapper {
      font-size: clamp(0.6rem, 2vw, 1rem);
     font-size: 0.43rem;
      top: 10%; /* 텍스트 위치 조정 */
      left: 65%;
      width: 90%; /* 화면 크기에 맞게 텍스트 영역 축소 */
    }
  
    .about-text-wrapper p {
      word-break: break-word;
      font-size: 0.4rem; /* 문단 글씨 크기 */
      line-height: 1.6; /* 줄 간격 */
      margin-bottom: 10px; /* 문단 간 여백 */
    }
  
    .about-text-wrapper ul {
       /* 리스트 들여쓰기 */
      margin-bottom: 10px; /* 리스트 간 여백 */
    }
  
    .about-text-wrapper ul ul {
      padding-left: 15px; /* 하위 리스트 들여쓰기 */
    }

    .about-text-wrapper p {
        word-break: break-word;
      font-size: 0.4rem; /* 작은 화면에서는 글씨 크기 줄임 */
    }
  }

  @media (max-width: 1000px) {
    .about-text-wrapper {
      font-size: clamp(0.2rem, 2vw, 0.5rem);
      margin-left: -5px;
      top: 10%; /* 텍스트 위치 조정 */
      left: 65%;
      width: 90%; /* 화면 크기에 맞게 텍스트 영역 축소 */
    }
  
    .about-text-wrapper p {
      margin-top: -10px;
      margin-left: -5px;
      font-size: clamp(0.2rem, 2vw, 0.5rem);
        word-break: break-word;
        overflow-wrap: break-word;
        white-space: normal;
    }
  }
  