.projects-section {
  position: relative;
  z-index: 1;
  border: 5px dotted pink;
  border-radius: 100px;
  margin: 200px auto;
  padding: 40px;
  box-sizing: border-box;
  text-align: center;
  max-width: 1200px;

  box-sizing: border-box;
}

.projects-grid {
  display: grid;
  grid-template-columns:  repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
  align-items: center;
}
.projects-grid .project-card:last-child {
  grid-column: 1 / -1;
  margin: 0 auto;
}



@media (max-width: 1000px) {
  .projects-grid {
    grid-template-columns: repeat(1, 1fr); /* 한 줄에 한 개씩 */
  }

  .project-card {
    width: 300px !important; /* 더 작은 크기 */
    height: 200px !important;
    margin: 0 auto; /* 카드 중앙 정렬 */
  }
}

.project-card {
  font-family: 'Moneygraphy-Rounded';
  font-weight: bold;
  perspective: 1000px;
  width: 400px;
  height: 250px;
  cursor: pointer;
  font-size: clamp(13px, 1.5vw, 14px);
}

.project-card .project-front,
.project-card .project-back {
  box-shadow: 0 5px 15px rgba(255, 111, 221, 0.499);
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transition: transform 0.8s ease;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.project-card .project-front {
  background: #fff;
}

.project-card .project-front img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  object-fit: cover;
}

.project-card:hover{
  transform: scale(1.1);
  
}

.project-card .hover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 10px;
}

.project-card .project-front:hover .hover-overlay {
  opacity: 1;
  transform: scale(1.0);
}

.project-card .project-back {
  background: #fff6fd;
  transform: rotateY(180deg);
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-card .buttons {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  justify-content: center;
}

.project-card .source-code-button,
.project-card .demo-button {
  color: white;
  text-decoration: none;
  padding: 8px 12px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.project-card .source-code-button {
  background-color: #ffcccb;
  margin-bottom: 15px;
}

.project-card .demo-button {
  background-color: #add8e6;
  margin-bottom: 15px;
}

.project-card.flipped .project-front {
  transform: rotateY(180deg);
}

.project-card.flipped .project-back {
  transform: rotateY(0deg);
}
